const TipInfo = styled('div')`
  display: flex;
  align-items: center;
  background: rgba(0, 0, 0, 0.03);
  border-radius: 8px;
  padding: 16px 20px;
  p {
    line-height: 20px;
    font-size: 14px;
    font-family: PingFangSC-Regular, sans-serif;
    margin-left: 16px;
    color: rgba(0, 0, 0, 0.5);
  }
`

const RED = 'red'

enum NeutralizeType {
  Wallet = 'wallet',
  Avatar = 'avatar',
}

const { ActionUser: fbtc20mint } = contracts
function NeutralizeComp() {
  const { setRefresh, walletTokenBalance } = useHashPowerToken()
  const { signer } = useSystemParams()
  const { active, chainId, account } = useWeb3React()
  const [approveStatus, setApproveStatus] = useState(false)
  const [disabledAction, setDisabledAction] = useState(true)
  const [neutralizeAmount, setNeutralizeAmount] = useState(0)
  const posContract = usePosContract(active, signer)
  const [neutralizeType, setNeutralizeType] = useState<NeutralizeType>(NeutralizeType.Wallet)

  const { toastSuccess, toastError, toastInfo } = useToast()
  const { currentMintToken, currentPosition, currentPositionTokenInfo, collateralIndex } = usePositionInfo()
  const { onUpdateNeutralizeValue, newNeutralizeFee, newDisplayCollateralRatio, newDisplayPrepaidInterestCoverage, newEstimatedDailyDebt } =
    useComputedIndexAction()

  const { handleUpdatePosition } = usePositionAction()

  const { handleApprove } = useApprove(account, currentPosition?.mintTokenInfo.address)
  const { onCheckApprove } = useCheckApprove()

  const onApprove = useCallback(async () => {
    try {
      const info = await handleApprove(currentPosition?.positionAddress)
      setApproveStatus(true)
      if (info.hash) {
        const result = await info.wait()
        if (result) {
          toastSuccess('Approve Success')
        }
      }
    } catch ({ message }: any) {
      setApproveStatus(false)
      toastError(ToastTitle.failed, `${message}`)
    }
  }, [handleApprove, currentPosition?.positionAddress, toastSuccess, toastError])

  const settlementCurrencyValues = useMemo(() => {
    const settlementCurrency = currentPosition?.mintTokenInfo.settlementCurrency
    if (currentPositionTokenInfo?.allowedTokens) {
      return {
        [settlementCurrency.symbol]: Number(
          TokenUtils.formatFourDecimals(
            Number(
              toLargeUnits(
                currentPositionTokenInfo.allowedTokens.balances[settlementCurrency.symbol],
                settlementCurrency.decimals,
              ),
            ),
          ),
        ),
        [currentMintToken]: Number(
          TokenUtils.formatFourDecimals(
            Number(
              toLargeUnits(
                currentPositionTokenInfo.allowedTokens.balances[currentPosition.mintTokenInfo.symbol],
                currentPosition.mintTokenInfo.decimals,
              ),
            ),
          ),
        ),
      }
    }
    return {
      [settlementCurrency.symbol]: 0,
      [currentMintToken]: 0,
    }
  }, [currentMintToken, currentPosition, currentPositionTokenInfo])

  const checkApprove = useCallback(async () => {
    if (currentPosition.isEnable) {
      const txInfo = await onCheckApprove(
        account,
        currentPosition.mintTokenInfo.address,
        currentPosition.positionAddress,
      )
      setApproveStatus(txInfo && txInfo._hex > 0)
    }
  }, [account, currentPosition, onCheckApprove])

  const onChangeNeutralizeAmount = useCallback(
    ({ target }) => {
      const { value } = target
      const _decimals = currentPosition.mintTokenInfo.decimals
      setNeutralizeAmount(value)
      const actionVal = value ? Number(`-${value}`) : 0
      onUpdateNeutralizeValue(actionVal)
      const debt = toLargeUnits(currentPosition.balance, currentPosition.mintTokenInfo.decimals)
      const isWaletValueVaild = value < walletTokenBalance[currentMintToken] && value > 0 && value <= debt
      const isCollateralValueVaild = value < settlementCurrencyValues[currentMintToken] && value > 0 && value <= debt

      if (neutralizeType === NeutralizeType.Wallet) {
        setDisabledAction(!isWaletValueVaild)
      } else {
        setDisabledAction(!isCollateralValueVaild)
      }
      if (value > 0) {
        const transferValue = toSmallUnits(
          TokenUtils.formatTokenAmount(value, currentPosition.mintTokenInfo.decimals),
          _decimals,
        )

        if (neutralizeType === NeutralizeType.Wallet) {
          setDisabledAction(!isWaletValueVaild || transferValue.isZero())
        } else {
          setDisabledAction(!isCollateralValueVaild || transferValue.isZero())
        }
      }
    },
    [
      onUpdateNeutralizeValue,
      settlementCurrencyValues,
      currentMintToken,
      walletTokenBalance,
      neutralizeType,
      currentPosition,
    ],
  )

  const onNeutralize = useCallback(async () => {
    if (!disabledAction) {
      const interf = new utils.Interface(fbtc20mint.abi)
      const data = interf.encodeFunctionData('neutralizetBalance', [
        toSmallUnits(String(neutralizeAmount), currentPosition.mintTokenInfo.decimals),
        neutralizeType === NeutralizeType.Wallet,
      ])
      if (posContract) {
        try {
          const info = await posContract.delegateCall(fbtc20mint.address[chainId], data)
          if (info) {
            setDisabledAction(true)
            setNeutralizeAmount(0)
            toastInfo(ToastTitle.submited)
            const result = await info.wait()
            if (result) {
              setRefresh((state) => !state)
              toastSuccess(ToastTitle.success)
              setDisabledAction(false)
              onUpdateNeutralizeValue(0)
              await handleUpdatePosition()
            }
          }
        } catch (e: any) {
          toastError(ToastTitle.failed, e?.reason || e?.message)
        }
      }
    }
  }, [
    disabledAction,
    neutralizeAmount,
    currentPosition.mintTokenInfo.decimals,
    neutralizeType,
    posContract,
    chainId,
    toastInfo,
    setRefresh,
    toastSuccess,
    onUpdateNeutralizeValue,
    handleUpdatePosition,
    toastError,
  ])

  useEffect(() => {
    checkApprove()
  }, [checkApprove])

  return (
    <>
      <SelectGroup>
        <Select
          selectValue={currentMintToken}
          groupStyle={{ width: '100%' }}
          options={[
            {
              ...currentPosition?.mintTokenInfo,
              token: currentPosition.mintTokenInfo.symbol,
            },
          ]}
          actionValue={neutralizeAmount}
          selectInput={onChangeNeutralizeAmount}
          isGroup
        />
        <Func dir="rtl">
          <ButtonBase
            onClick={() => {
              const debt = toLargeUnits(currentPosition.balance, currentPosition.mintTokenInfo.decimals)
              if (neutralizeType === NeutralizeType.Wallet) {
                const value = Math.min(Number(walletTokenBalance[currentMintToken]), Number(debt))
                onChangeNeutralizeAmount({ target: { value } })
              } else {
                const value = Math.min(Number(settlementCurrencyValues[currentMintToken]), Number(debt))
                onChangeNeutralizeAmount({ target: { value } })
              }
            }}
          >
            Max
          </ButtonBase>
        </Func>
        <SelectTypeWrap>
          <h3>Neutralize the asset from</h3>
          <div className="content">
            <div
              className={`item_wrap ${neutralizeType === NeutralizeType.Wallet ? 'active' : ''}`}
              onClick={() => {
                setNeutralizeType(NeutralizeType.Wallet)
                if (neutralizeAmount > 0) {
                  setDisabledAction(
                    newNeutralizeFee > walletTokenBalance.WBTC ||
                      neutralizeAmount > walletTokenBalance[currentMintToken],
                  )
                }
              }}
              onKeyUp={() => null}
              tabIndex={0}
              role="button"
            >
              <div className="item wallet">
                <h4>
                  <WalletIcon /> My Wallet
                </h4>
                <p>
                  <span>{currentMintToken}</span>
                  <span
                    style={{
                      color:
                        (neutralizeType === NeutralizeType.Wallet &&
                          walletTokenBalance &&
                          neutralizeAmount > walletTokenBalance[currentMintToken] &&
                          RED) ||
                        '',
                    }}
                  >
                    {(walletTokenBalance && TokenUtils.formatFourDecimals(walletTokenBalance[currentMintToken])) || 0}
                  </span>
                </p>
                <p>
                  <span>{currentPosition.mintTokenInfo.settlementCurrency.symbol}</span>
                  <span
                    style={{
                      color:
                        neutralizeType === NeutralizeType.Wallet &&
                        walletTokenBalance &&
                        newNeutralizeFee > walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol]
                          ? RED
                          : '',
                    }}
                  >
                    {(walletTokenBalance && TokenUtils.formatFourDecimals(walletTokenBalance.WBTC)) || 0}
                  </span>
                </p>
              </div>
            </div>

            <div
              className={`item_wrap ${neutralizeType === NeutralizeType.Avatar ? 'active' : ''}`}
              onClick={() => {
                setNeutralizeType(NeutralizeType.Avatar)
                if (neutralizeAmount > 0) {
                  setDisabledAction(
                    neutralizeAmount > settlementCurrencyValues[currentMintToken] ||
                      newNeutralizeFee >
                        settlementCurrencyValues[currentPosition.mintTokenInfo.settlementCurrency.symbol],
                  )
                }
              }}
              onKeyUp={() => null}
              tabIndex={0}
              role="button"
            >
              <div className="item avatar">
                <h4>
                  <AvatarIcon />
                  Collateral
                </h4>
                <p>
                  <span>{currentMintToken}</span>
                  <span
                    style={{
                      color:
                        (neutralizeType === NeutralizeType.Avatar &&
                          neutralizeAmount > settlementCurrencyValues[currentMintToken] &&
                          RED) ||
                        '',
                    }}
                  >
                    {settlementCurrencyValues[currentPosition.mintTokenInfo.symbol]}
                  </span>
                </p>
                <p>
                  <span>{currentPosition.mintTokenInfo.settlementCurrency.symbol}</span>
                  <span
                    style={{
                      color:
                        (neutralizeType === NeutralizeType.Avatar &&
                          newNeutralizeFee >
                            settlementCurrencyValues[currentPosition.mintTokenInfo.settlementCurrency.symbol] &&
                          RED) ||
                        '',
                    }}
                  >
                    {settlementCurrencyValues[currentPosition.mintTokenInfo.settlementCurrency.symbol]}
                  </span>
                </p>
              </div>
            </div>
          </div>
        </SelectTypeWrap>
      </SelectGroup>
      <Tip>
        {newEstimatedDailyDebt}
        <table className="info_panel" cellSpacing={20} cellPadding={20}>
          <thead>
            <tr>
              <td className="from" colSpan={2}>
                From
              </td>
              <td className="to">To</td>
            </tr>
            <tr>
              <td className="label">Outstanding Debt</td>
              <td className="from">
                {TokenUtils.formatFourDecimals(
                  Number(toLargeUnits(currentPosition?.balance, currentPosition?.mintTokenInfo.decimals)),
                )}
              </td>
              <td className="to">
                {TokenUtils.formatFourDecimals(
                  Number(toLargeUnits(currentPosition?.balance, currentPosition?.mintTokenInfo.decimals)) -
                    neutralizeAmount,
                )}
              </td>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td className="label">{DataIndexName.CollateralRatio}</td>
              <td className="from">{collateralIndex.displayRatio}</td>
              <td className="to">{newDisplayCollateralRatio}</td>
            </tr>
            <tr>
              <td className="label">{DataIndexName.PrepaidInterestCoverage}</td>
              <td className="from">{collateralIndex.displayCoverage}</td>

              <td className="to">{newDisplayPrepaidInterestCoverage}</td>
            </tr>
            <tr className="footer">
              <td className="label">Estimate neutralize fee</td>
              <td
                className="to"
                colSpan={2}
                style={{
                  color:
                    (neutralizeType === NeutralizeType.Wallet &&
                      walletTokenBalance &&
                      newNeutralizeFee > walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol]) ||
                    (neutralizeType === NeutralizeType.Avatar &&
                      newNeutralizeFee >
                        settlementCurrencyValues[currentPosition.mintTokenInfo.settlementCurrency.symbol])
                      ? RED
                      : '',
                }}
              >
                {!newNeutralizeFee ? 0 : `${newNeutralizeFee}`} WBTC
              </td>
            </tr>
          </tbody>
        </table>
      </Tip>
      <TipInfo>
        {(neutralizeType === NeutralizeType.Wallet &&
          walletTokenBalance &&
          newNeutralizeFee > walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol]) ||
        (neutralizeType === NeutralizeType.Avatar &&
          newNeutralizeFee > settlementCurrencyValues[currentPosition.mintTokenInfo.settlementCurrency.symbol]) ? (
          <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path
              d="M8 16C12.4183 16 16 12.4183 16 8C16 3.58172 12.4183 0 8 0C3.58172 0 0 3.58172 0 8C0 12.4183 3.58172 16 8 16Z"
              fill="#FF2E00"
            />
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M8.57852 10.156C8.82398 10.156 9.02296 10.355 9.02296 10.6004V11.756C9.02296 12.0015 8.82398 12.2004 8.57852 12.2004H7.42296C7.1775 12.2004 6.97852 12.0015 6.97852 11.756V10.6004C6.97852 10.355 7.1775 10.156 7.42296 10.156H8.57852ZM8.57852 3.62842C8.82398 3.62842 9.02296 3.8274 9.02296 4.07286V8.74859C9.02296 8.99405 8.82398 9.19303 8.57852 9.19303H7.42296C7.1775 9.19303 6.97852 8.99405 6.97852 8.74859V4.07286C6.97852 3.8274 7.1775 3.62842 7.42296 3.62842H8.57852Z"
              fill="white"
            />
          </svg>
        ) : (
          <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path
              d="M8 16C12.4183 16 16 12.4183 16 8C16 3.58172 12.4183 0 8 0C3.58172 0 0 3.58172 0 8C0 12.4183 3.58172 16 8 16Z"
              fill="#73797C"
            />
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M7.42295 5.6736C7.17749 5.6736 6.9785 5.47461 6.9785 5.22915V4.0736C6.9785 3.82814 7.17749 3.62915 7.42295 3.62915H8.5785C8.82396 3.62915 9.02295 3.82814 9.02295 4.0736L9.02295 5.22915C9.02295 5.47461 8.82396 5.6736 8.5785 5.6736H7.42295ZM7.42295 12.2012C7.17749 12.2012 6.9785 12.0022 6.9785 11.7567V7.081C6.9785 6.83554 7.17749 6.63656 7.42295 6.63656H8.5785C8.82396 6.63656 9.02295 6.83554 9.02295 7.081V11.7567C9.02295 12.0022 8.82396 12.2012 8.5785 12.2012H7.42295Z"
              fill="white"
            />
          </svg>
        )}

        <p
          style={{
            color:
              (neutralizeType === NeutralizeType.Wallet &&
                walletTokenBalance &&
                newNeutralizeFee > walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol]) ||
              (neutralizeType === NeutralizeType.Avatar &&
                newNeutralizeFee > settlementCurrencyValues[currentPosition.mintTokenInfo.settlementCurrency.symbol])
                ? RED
                : '',
          }}
        >
          A 1.0% fee of the token value will be levied after the position is neutralized.
        </p>
      </TipInfo>
      {neutralizeType === NeutralizeType.Avatar ? (
        <NeutralizeButton disabled={disabledAction} onClick={onNeutralize}>
          Neutralize
        </NeutralizeButton>
      ) : (
        <>
          {approveStatus ? (
            <NeutralizeButton disabled={disabledAction} onClick={onNeutralize}>
              Neutralize
            </NeutralizeButton>
          ) : (
            <NeutralizeButton disabled={approveStatus} onClick={onApprove}>
              Approve
            </NeutralizeButton>
          )}
        </>
      )}
    </>
  )
}

export default NeutralizeComp
